<template>
      <h2>Welcome, {{ teamName }}!</h2>
      <p style="margin-bottom: 5px;">Use the form below to vote for each category (0 - 10).</p>
      <span @click="showHint" class="hint-button">Hint</span>  
      <form @submit.prevent="submitVote">
          <label for="team">Team: <br><span>(Pick the name of the team who's cocktail you've just tasted)</span></label>
          <select id="team" v-model="formData.team">
            <option disabled value="">-- Choose team --</option>
            <option v-for="team in teams" :key="team" :value="team">{{ team }}</option>
          </select>

          <label for="taste">Taste:</label>
          <select id="taste" v-model="formData.taste"> 
            <option disabled value="">-- Choose points --</option>
            <option v-for="n in 11" :key="n" :value="n-1">{{ n-1 }}</option>
          </select>

          <label for="presentation">Presentation:</label>
          <select id="presentation" v-model="formData.presentation">
            <option disabled value="">-- Choose points --</option>
            <option v-for="n in 11" :key="n" :value="n-1">{{ n-1 }}</option>
          </select>

          <label for="christmasness">Christmasiness:</label>
          <select id="christmasness" v-model="formData.christmasness">
            <option disabled value="">-- Choose points --</option>
            <option v-for="n in 11" :key="n" :value="n-1">{{ n-1 }}</option>
          </select>

          <button type="submit" class="submit-button">Submit</button>
      </form>
</template>

<style>
    form {
        display: flex;
        flex-direction: column;
        text-align: left;
        @media (max-width: 450px) {
          margin-top: 40px;
        }
        select {
            background: #ffffff;
            color: #000;
            border-radius: 16px;
            padding: 20px;
            width: auto;
            margin-top: 12px;
            margin-bottom: 18px;
            border: 1px solid #999999;
            &::placeholder {
            color: #000;
            }
            &::after {
                display: none;
            }
            appearance: none;
            background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23131313%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
            background-repeat: no-repeat;
            background-position: right 20px top 50%;
            background-size: 0.65rem auto;
        }
        span {
            color: #c62118;
            font-size: 14px;
        }
    }
    .hint-button {
      color: #c62118;
      font-size: 16px;
      text-decoration: underline;
      transition: ease .3s;
      position: relative;
      right: 0;
      text-align: right;
      width: 100%;
      display: block;
      &:hover {
          color: #a81613;
      }
    }
  </style>

<script>
  export default {
    name: 'QuestionsVue',
    data() {
      return {
        teamName: this.$route.query.team || '',
        formData: {
          team: '',
          taste: '',
          presentation: '',
          christmasness: '',
        },
        teams: ['Hannah, Louis & Jack', 'Dom & Mary', 'Josh, Elise & Peter', 'Jonny & Emily'],
      };
    },
    methods: {
      async submitVote() {
        const webhookUrl = 'https://chat.googleapis.com/v1/spaces/AAAAm13_PNw/messages?key=AIzaSyDdI0hCZtE6vySjMm-WEfRq3CPzqKqqsHI&token=iyukcWwj3CH6O7EQaUuDUuGsX0IMMGobdluwkMYCnfQ';
        const message = {
          text: `New vote received:\n\nTeam Name: ${this.teamName}\nTeam: ${this.formData.team}\nTaste: ${this.formData.taste}\nPresentation: ${this.formData.presentation}\nChristmasness: ${this.formData.christmasness}`,
        };
  
        try {
          const response = await fetch(webhookUrl, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(message),
          });
  
          if (!response.ok) {
            throw new Error(`Error sending message: ${response.statusText}`);
          }
  
          alert('Thanks for voting! Your response has been sent.');
          this.resetForm();
        } catch (error) {
          console.error(error);
          alert('There might have been an error with your submission, please check with Jonny that he has recieved your vote.');
        }
      },
      resetForm() {
        this.formData = {
          team: '',
          taste: '',
          presentation: '',
          christmasness: '',
        };
      },
      showHint() {
        alert('0 is Awful - 10 is Amazing, DUH...');
      },
    },
  };
</script>
  